    }
}


.icon-animation-body .icon-container .icon.leaf path {
    stroke-dasharray: 2350;
    stroke-dashoffset: 0;
}

.icon-animation-body .icon-container .icon.leaf:hover path {
    -webkit-animation: animate-leaf 1.5s linear forwards;
            animation: animate-leaf 1.5s linear forwards;
}

@-webkit-keyframes animate-leaf {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 2350;
    }
    80% {
        stroke-dashoffset: 4700;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 4700;
