/* Image Slide Left Card Demo */
.image-slide-left-card-demo {
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #222;
}

.image-slide-left-card-demo .container {
    position: relative;
    width: 1000px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 20px;
}

.image-slide-left-card-demo .container .card {
    position: relative;
    height: 250px;
    background: white;
    display: flex;
    width: 45%;
    margin: 30px 0;
}

.image-slide-left-card-demo .container .card .imgBx {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #333;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: .5s ease-in-out;
}

.image-slide-left-card-demo .container .card:hover .imgBx {
    width: 150px;
    height: 150px;
    left: -75px;
    top: calc(50% - 75px);
    transition: .5s ease-in-out;
    background: #ff0057;
}

.image-slide-left-card-demo .container .card .imgBx:before {
    content: attr(data-text);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 6em;
    color: rgba(255, 255, 255, .05);
    font-weight: 700;
}

.image-slide-left-card-demo .container .card .imgBx svg {
    color: white;
    height: 65%;
    width: 65%;
    transition: .5s ease-in-out;
}

.image-slide-left-card-demo .container .card:hover .imgBx svg {
    height: 55%;
    width: 55%;
}

.image-slide-left-card-demo .container .card .content {
    position: absolute;
    right: 0;
    width: calc(100% - 75px);
    height: 100%;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-slide-left-card-demo .container .card .content h3 {
    margin-bottom: 5px;
    font-size: 24px;
}

.image-slide-left-card-demo .container .card .content a {
    display: inline-block;
    margin-top: 10px;
    padding: 5px 10px;
    background: #333;
    text-decoration: none;
    color: white;
}

@media (max-width: 992px) {
    .image-slide-left-card-demo .container {
        width: 100%;
        flex-direction: column;
        align-items: center;
    }

    .image-slide-left-card-demo .container .card {
        width: 400px;
    }
}

@media (max-width: 768px) {
    .image-slide-left-card-demo .container .card {
        max-width: 300px;
        flex-direction: column;
        height: auto;
    }

    .image-slide-left-card-demo .container .card .imgBx {
        position: relative;
    }

    .image-slide-left-card-demo .container .card .imgBx,
    .image-slide-left-card-demo .container .card:hover .imgBx {
        width: 100%;
        height: 200px;
        left: 0;
        top: 0;
    }

    .image-slide-left-card-demo .container .card .imgBx svg,
    .image-slide-left-card-demo .container .card:hover .imgBx svg {
        max-width: 100px;
    }

    .image-slide-left-card-demo .container .card .content {
        position: relative;
        width: 100%;
    }
}
.remote-navbar-demo {
    background: #f2f2f2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.remote-navbar-demo ul {
    position: relative;
    transform: rotate(-25deg) skew(20deg, 5deg);
    list-style-type: none;
}

.remote-navbar-demo .list-item {
    background: #000;
    color: #575757;
    text-align: center;
    height: 2.5em;
    width: 4em;
    line-height: 2.5em;
    border-bottom: 1px solid #060606;
    position: relative;
    display: block;
    text-decoration: none;
    box-shadow: -2em 1.5em 0 #e1e1e1;
    transition: all 0.25s linear;
}

.remote-navbar-demo .list-item:hover {
    background: #ff6e42;
    color: #fffcfb;
    transform: translate(0.9em, -0.9em);
    transition: all 0.25s linear;
    box-shadow: -2em 2em 0 #e1e1e1;
}

.remote-navbar-demo .list-item:hover:before, 
.remote-navbar-demo .list-item:hover:after {
    transition: all 0.25s linear;
}

.remote-navbar-demo .list-item:hover:before {
    background: #b65234;
    width: 1em;
    top: 0.5em;
    left: -1em;
}

.remote-navbar-demo .list-item:hover:after {
    background: #b65234;
    width: 1em;
    bottom: -2.5em;
    left: 1em;
    height: 4em;
}

.remote-navbar-demo .list-item:before, .list-item:after {
    content: '';
    position: absolute;
    transition: all 0.25s linear;
    width: 0.5em;
}

.remote-navbar-demo .list-item:after {
    height: 4em;
    background: #181818;
    bottom: -2.25em;
    left: 1.5em;
    transform: rotate(90deg) skew(0, 45deg);
}

.remote-navbar-demo .list-item:before {
    height: 2.5em;
    background: #121212;
    top: 0.25em;
    left: -0.5em;
    transform: skewY(-45deg);
}

